import {
	UPDATE_WINDOW_WIDTH_PROPS
} from 'actionTypes';

const initialState = {};

export default (state = initialState, {type, payload}) => {
	switch(type) {
		case UPDATE_WINDOW_WIDTH_PROPS:
			return payload < 1180 ? true : false;
		default:
			return state;
	}
}


