import {
	FETCH_SETTINGS_SUCCESS
} from 'actionTypes';

const initialState = {};

export default (state = initialState, {type, payload}) => {
	switch(type) {
		case FETCH_SETTINGS_SUCCESS:
			return { ...state, ...payload };
		default:
			return state;
	}
}


